@import url(https://fonts.googleapis.com/css?family=Audiowide&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box; }

body {
  color: #BBE6F5;
  background-color: #15202b;
  font-family: "Open Sans", sans-serif;
  line-height: 1.25; }

h1, h2, h3, h4, h5, h6 {
  color: #BBE6F5;
  font-family: "Open Sans", sans-serif;
  margin: 20px 0;
  font-weight: 700; }

h1 {
  font-size: 1.5em; }

h2 {
  font-size: 1.25em; }

h3 {
  font-size: 1em; }

h4 {
  font-size: .75em; }

p {
  color: #BBE6F5;
  font-family: "Open Sans", sans-serif;
  margin: .05em 0; }

a {
  text-decoration: none;
  cursor: pointer; }

.component-container {
  width: 100%;
  max-width: 600px;
  text-align: center;
  margin: auto;
  padding: 1.25em; }

form {
  font: "Open Sans", sans-serif;
  margin: 10px auto 0 auto;
  padding: 16px; }

form input[type="text"],
form input[type="date"],
form input[type="datetime"],
form input[type="email"],
form input[type="number"],
form input[type="search"],
form input[type="time"],
form input[type="url"],
form textarea,
form select {
  outline: none;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  border-radius: 3px;
  background: #fff;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  padding: 10px;
  color: #555;
  font: "Open Sans", sans-serif; }

form input[type="text"]:focus,
form input[type="date"]:focus,
form input[type="datetime"]:focus,
form input[type="email"]:focus,
form input[type="number"]:focus,
form input[type="search"]:focus,
form input[type="time"]:focus,
form input[type="url"]:focus,
form textarea:focus,
form select:focus {
  box-shadow: 0 0 5px #0f3966;
  padding: 10px;
  border: 1px solid #0f3966; }

form input[type="submit"],
form input[type="button"], button {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  padding: 10px;
  background: #48798A;
  border-bottom: 2px solid #0f3966;
  border-top-style: none;
  border-right-style: none;
  border-left-style: none;
  color: #BBE6F5;
  border-radius: 3px;
  clear: both; }

form input[type="submit"]:hover,
form input[type="button"]:hover, button:hover {
  background: #2EBC99; }

select {
  -webkit-appearance: menulist-button;
  height: 35px; }

#Review .movie-info {
  display: flex; }
  #Review .movie-info img {
    width: 150px; }
  #Review .movie-info .movie-poster {
    margin-right: 1.5em; }
  #Review .movie-info #synopsis {
    text-align: left;
    font-size: .8em;
    padding: .4em;
    background-color: #48798A;
    border-radius: .2em; }
  #Review .movie-info h1, #Review .movie-info h2, #Review .movie-info h3, #Review .movie-info h4 {
    text-align: left; }
  #Review .movie-info h1 {
    margin-top: 0; }
  #Review .movie-info h4 {
    margin-bottom: 0; }

#Review #review-text-box {
  color: #BBE6F5;
  background-color: #324d67;
  border: #2a4055 2px solid; }

#Review .thumbsUpOrDown {
  display: flex; }
  #Review .thumbsUpOrDown .rating {
    width: 100%;
    margin-top: 15px;
    margin-bottom: 15px;
    text-align: center; }
  #Review .thumbsUpOrDown .like,
  #Review .thumbsUpOrDown .dislike {
    display: inline-block;
    cursor: pointer;
    margin: 10px; }
  #Review .thumbsUpOrDown .dislike {
    transform: scaleX(-1);
    -moz-transform: scaleX(-1);
    -webkit-transform: scaleX(-1);
    -ms-transform: scaleX(-1); }
  #Review .thumbsUpOrDown .active {
    color: #2EBDD1;
    -webkit-transform: scale(1.2);
            transform: scale(1.2); }

#login {
  margin-top: 4em;
  text-align: center;
  color: #BBE6F5;
  background-color: #15202b;
  font-family: "Open Sans", sans-serif; }
  #login body {
    color: #BBE6F5;
    background-color: #15202b;
    font-family: "Open Sans", sans-serif; }
  #login h1 {
    margin-bottom: 20px;
    font-size: 30px; }
  #login button, #login form input[type="text"] {
    width: 100%; }
  #login form {
    max-width: 400px; }
  #login #username-entry {
    background-color: #2F6A84; }

#dashboard {
  color: #BBE6F5;
  padding-bottom: 70px;
  background-color: #15202b;
  font-family: "Open Sans", sans-serif;
  line-height: 1.25; }
  #dashboard span {
    display: flex; }
  #dashboard i.fa-twitter {
    font-size: 27px;
    position: relative;
    bottom: 6px;
    margin-left: 15px;
    color: #00aced; }
  #dashboard #react-tab {
    width: 100%;
    margin: auto;
    border: 0.1em solid #2a4055;
    border-radius: .2em; }
  #dashboard #react-tab > div > div > div {
    background-color: #324d67;
    border: #2a4055 1px solid;
    border-bottom: none; }
  #dashboard #react-tabtab-tab-0, #dashboard #react-tabtab-tab-1 {
    color: #BBE6F5;
    background-color: #436689;
    padding: 10px 30px; }
  #dashboard i.fa-thumbs-up, #dashboard i.fa-film {
    margin-right: 5px;
    position: relative;
    top: 2px; }
  #dashboard #react-tabtab-tab-0[aria-selected="true"], #dashboard #react-tabtab-tab-1[aria-selected="true"] {
    border: #2a4055 solid 1px; }
  #dashboard #react-tabtab-panel-0 > ul > li, #dashboard #react-tabtab-panel-1 > ul > li {
    background-color: #436689; }
  #dashboard #react-tabtab-panel-0, #dashboard #react-tabtab-panel-1 {
    background-color: #2F6A84; }
  #dashboard form {
    position: fixed;
    bottom: 0;
    left: 0;
    text-align: center;
    background-color: #223344;
    padding: 10px;
    width: 100%; }
  #dashboard form input[type="text"] {
    width: 75%;
    max-width: 370px;
    margin-right: 10px;
    margin-bottom: 0; }
  #dashboard button {
    width: 20%;
    max-width: 80px; }
  #dashboard .profile-pic {
    width: 120px;
    padding: 20px;
    background-color: #2F6A84;
    border-radius: 60px; }
  #dashboard h2 {
    margin: 0;
    font-weight: 700;
    font-size: 1em; }
  #dashboard .suggested-list, #dashboard .movie-list {
    margin-top: 1.5em; }
    #dashboard .suggested-list img, #dashboard .movie-list img {
      width: 80px; }
    #dashboard .suggested-list .synopsis, #dashboard .movie-list .synopsis {
      font-size: 0.8em;
      float: left;
      text-align: left; }
    #dashboard .suggested-list li, #dashboard .movie-list li {
      display: flex;
      margin-bottom: 1.5em;
      border: #2a4055 1px solid;
      border-radius: .3em;
      padding: 1em; }
    #dashboard .suggested-list .movie-poster, #dashboard .suggested-list .review-poster, #dashboard .movie-list .movie-poster, #dashboard .movie-list .review-poster {
      margin-right: 1em; }
    #dashboard .suggested-list h3, #dashboard .movie-list h3 {
      margin-top: 0;
      margin-bottom: 0;
      text-align: left; }

#AboutUs img {
  width: 150px;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 75px; }

#AboutUs hr {
  margin: 50px 150px;
  height: 1px;
  border: none;
  color: #e4cc46;
  background-color: #e4cc46; }

nav {
  color: #BBE6F5;
  font-family: "Open Sans", sans-serif; }

nav > link,
nav > a {
  color: #BBE6F5;
  text-decoration: none; }

nav link:visited,
nav a:visited {
  color: #BBE6F5; }

nav > ul > a {
  margin-left: 0.5em;
  margin-right: 0.5em;
  text-decoration: none; }

#navbar-links li {
  color: #BBE6F5;
  font-weight: bold;
  text-align: right; }

#navbar-links:focus {
  border: none;
  outline: none; }

#title-logo {
  position: relative;
  display: inline-block;
  width: 7.5em;
  height: 5.25em;
  margin: 1em 1.2em;
  background: #0e2e3e;
  border-radius: 50% / 10%;
  box-shadow: 1px 1px 6px 6px #00f7ff;
  color: #272E46;
  line-height: 4em;
  text-align: center;
  text-indent: 0.1em;
  text-shadow: -1px -1px 5px #00f7ff, 1px 1px 5px #00f7ff; }

#title-logo:before {
  font-size: 2em;
  font-family: 'Audiowide', cursive;
  content: 'VEWR';
  position: absolute;
  top: 10%;
  bottom: 10%;
  right: -5%;
  left: -5%;
  background: inherit;
  border-radius: 5% / 50%; }

/* Individual item */
.bm-item {
  display: inline-block;
  /* Our sidebar item styling */
  text-decoration: none;
  margin-bottom: 10px;
  color: #BBE6F5;
  -webkit-transition: color 0.2s;
  transition: color 0.2s; }

/* Change color on hover */
.bm-item:hover {
  color: white; }

/* Position and sizing of burger button */
.bm-burger-button {
  position: absolute;
  width: 36px;
  height: 30px;
  right: 5%;
  top: 5%; }

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #223344; }

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px; }

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7; }

/* General sidebar styles */
.bm-menu {
  background: #324d67;
  padding: 2.5em 1.5em 5em;
  font-size: 1.15em; }

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #324d67; }

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad; }

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3); }

#Search .movie-list img {
  width: 80px; }

#Search .movie-list .synopsis {
  font-size: 0.8em;
  float: left;
  text-align: left; }

#Search .movie-list li {
  display: flex;
  margin-bottom: 1.5em; }

#Search .movie-list .movie-poster {
  margin-right: 1em; }

#Search .movie-list h3 {
  margin-top: 0;
  margin-bottom: 0;
  text-align: left; }

